//layout Width boxed

[data-layout-width="boxed"] {

    body,
    .header {
        max-width: 1300px;
        margin: auto;
    }

    .sidebar {
        ul {
            .menu-title {
                display: none !important;
            }

            li {
                span {
                    display: none !important;
                }
            }

            ul {
                li {
                    display: none !important;
                }
            }
        }
    }

    .page-wrapper {
        margin: 0 0 0 230px;
    }

    .main-wrapper {
        position: relative;

        #toggle_btn {
            display: none;
        }
    }

    .sidebar {
        position: absolute;
        width: 230px;

        .noti-dot {
            display: none !important;
        }

        ul {
            .menu-title {
                display: block !important;
            }

            li {
                span {
                    display: block !important;
                }
            }

            ul {
                li {
                    display: block !important;
                }
            }
        }
    }
}

[data-sidebar-size="sm-hover"] {
    #toggle_btn {
        display: none;
    }

    .sidebar {

        ul {
            .menu-title {
                display: none !important;
            }

            li {
                span {
                    display: none !important;
                }
            }

            ul {
                li {
                    display: none !important;
                    width: 100%;
                }
            }
        }
    }

    .two-col-bar {
        &:hover {
            .sidebar-right {
                display: block;
            }
        }

        .sidebar-left {
            display: block;
        }

        .sidebar-right {
            display: none;
        }
    }

    .page-wrapper {
        margin: 0 0 0 60px;
    }

    .sidebar {
        width: 60px;

        .noti-dot {
            display: none !important;
        }

        .sidebar-menu {
            ul li span {
                display: none !important;
            }
        }

        &:hover {
            width: 230px;

            ul {
                .menu-title {
                    display: inline-block !important;
                }

                li {
                    span {
                        display: inline-block !important;
                    }
                }

                ul {
                    li {
                        display: inline-block !important;
                    }
                }
            }
        }
    }
}

[data-sidebar-size="md"] {
    .page-wrapper {
        margin: 0 0 0 200px;
    }

    .sidebar {
        width: 200px;

        .menu-title {
            justify-content: center;
        }

        .sidebar-menu ul li a {
            flex-direction: column;
            text-align: center;
            padding: 10px;
        }
    }

    #toggle_btn {
        display: none;
    }
}

[data-layout-position="scrollable"] {
    .main-wrapper {
        position: relative;

        .sidebar {
            position: absolute;
        }
    }

    .slimScrollDiv,
    .slimscroll {
        overflow: auto !important;
        height: auto !important;
    }
}

[data-sidebar="dark"] {
    &[data-layout-position="scrollable"] .slimscroll {
        background-color: #34444c;
    }

}

[data-layout="twocolumn"] {
    .two-col-bar {
        display: block;
    }

    #sidebar {
        display: none;

        @include respond-below(custom991) {
            display: block;
        }
    }

    #sidebar-view,
    #sidebar-size,
    #layout-width,
    #layout-position {
        display: none;
    }

    .mini-sidebar {
        .sidebar {
            .sidebar-right {
                display: none;
            }
        }

        &.expand-menu {
            .sidebar {
                .sidebar-right {
                    display: block;
                }
            }
        }
    }

}

[data-layout-width=boxed] {
    &[data-sidebar-size="md"] {
        .sidebar {
            &:hover {
                width: 200px;
            }

            ul {
                .menu-title {
                    display: block !important;
                    text-align: center;
                }

                li span {
                    display: block !important;
                }

                ul li {
                    display: block !important;
                }
            }
        }
    }
}

//Layout Light 

[data-layout-mode="light"] {
    &[data-topbar=dark] {
        body {
            .header {
                background: #263238;

                .has-arrow .dropdown-toggle:after {
                    border-bottom: 2px solid #fff;
                    border-right: 2px solid #fff;
                }
            }
        }

    }

    .header {
        background: $white;
        border-color: transparent;

        .page-title-box h3 {
            color: $secondary-900;
        }

        .logo {
            display: none;
        }

        .logo2 {
            display: inline-block;
            line-height: 60px;
        }

        #toggle_btn .bar-icon span {
            background-color: $secondary-900;
        }

        .top-nav-search form {
            .form-control {
                border: 1px solid $secondary-200;
                color: $secondary-900;
                height: 40px;
                padding: 10px 50px 10px 15px;
                border-radius: 5px;

                &::-webkit-input-placeholder {
                    /* Edge */
                    color: $secondary-900;
                }

                &:-ms-input-placeholder {
                    /* Internet Explorer 10-11 */
                    color: $secondary-900;
                }

                &::placeholder {
                    color: $secondary-900;
                }
            }

            .btn {
                color: $secondary-900;
            }
        }

        .has-arrow {
            .dropdown-toggle:after {
                border-bottom: 2px solid $secondary-900;
                border-right: 2px solid $secondary-900;
            }
        }
    }

    nav.greedy button {
        color: $secondary-900 !important;
    }

    .sidebar-twocol.sidebar .sidebar-left {
        background: $white;
        border-right: 1px solid $secondary-200;

        .nav-link {
            color: $secondary-900;

            &.active {
                background: $secondary-800;
                color: $white;
            }
        }
    }

    .sidebar {
        background-color: $white;
        box-shadow: 0 1px 1px 0 rgb(0 0 0 / 20%);

        .sidebar-menu {
            ul {
                ul {
                    display: none;
                    width: 200px;
                }

                li {
                    a:hover {
                        color: $secondary-800;
                    }
                }
            }

            .greedy {
                ul {
                    li {
                        a:hover {
                            color: $secondary-800;
                        }
                    }
                }
            }
        }

    }

    .user-menu.nav>li>a {
        color: $secondary-900;
    }

    .mini-sidebar .header-left .logo2 img {
        height: auto;
        max-height: 30px;
        width: auto;
    }

    .header .header-left .logo {
        display: none !important;
        line-height: 60px;
    }

    .sidebar-menu ul li a:hover,
    .two-col-bar .sidebar-menu ul li a:hover {
        color: $secondary-900;
    }
}

