[data-layout-mode="blue"] {
    .header {
        background: $blue !important;
        background: $blue-gradient !important;
        border-color: transparent;

        .header-left .logo {
            display: block;
            line-height: 60px;
        }

        .user-menu.nav>li>a {
            color: $white !important;
        }

        #toggle_btn .bar-icon span {
            background-color: $white;
        }

        .page-title-box h3 {
            color: $white;
        }

        .logo2 {
            display: none;
        }
    }

    .page-item.active {
        .page-link {
            background-color: $blue ;
            border-color: $blue ;
        }
    }

    .nav-tabs.nav-tabs-solid {
        li a.active {
            background-color: $blue;
            border-color: $blue;
        }
    }

    .sidebar-twocol.sidebar {
        .sidebar-left {
            background: $secondary-800;
        }

        .sidebar-right ul li a.active {
            color: $blue;
        }
    }

    #layout-position .radio input:checked+label {
        background: $blue;
        color: $white;
    }

    .dash-widget {
        .card-body {
            .dash-widget-icon {
                background-color: rgba(0, 197, 251, 0.2);
                color: $blue;
            }
        }
    }

    .bg-primary,
    .badge-primary {
        background-color: $blue !important;
    }

    .sidebar {

        .sidebar-menu {
            ul ul a.active {
                color: $blue;
                text-decoration: underline;
            }
        }

        ul li.submenu .noti-dot:before {
            border: 5px solid $blue;
        }

    }

    .btn-primary {
        background-color: $blue;
        border: 1px solid $blue;

        &:focus,
        &:hover,
        &:active,
        &.active {
            color: $white;
            background-color: $blue-hover;
            border-color: $blue-hover;

            .open {
                &>.dropdown-toggle {
                    &.btn-primary {
                        background-color: $blue-hover;
                        border: 1px solid $blue-hover;
                    }
                }
            }
        }

        &.active,
        .focus {

            &.focus,
            &:focus,
            &:hover {
                .open {
                    &>.dropdown-toggle {
                        &.btn-primary {

                            &.focus,
                            &:focus,
                            &:hover {
                                background-color: $blue-hover;
                                border: 1px solid $blue-hover;
                            }
                        }
                    }
                }
            }
        }

        &:active {

            &:focus,
            &:hover {
                background-color: $blue-hover;
                border: 1px solid $blue-hover;
            }
        }

        .active,
        &:active {
            &:not(:disabled):not(.disabled) {
                background-color: $blue-hover;
                border-color: $blue-hover;
                color: $white;
            }

            &:focus {
                &:not(:disabled):not(.disabled) {
                    box-shadow: unset;
                }
            }
        }

        .show {
            &>.btn-primary {
                &.dropdown-toggle {
                    background-color: $blue-hover;
                    border-color: $blue-hover;
                    color: $white;

                    &:focus {
                        box-shadow: unset;
                    }
                }
            }
        }

        &.disabled,
        &:disabled {
            background-color: $blue;
            border-color: $blue;
            color: $white;
        }
    }

    .roles-menu ul li.active a {
        border-color: $blue;
        color: $blue;
    }

    .settings-icon {
        span {
            background-color: $blue;
        }
    }

    .profile-view .profile-basic .profile-info-left .staff-msg .btn-custom {
        background: $blue-gradient;
    }

    .profile-view .profile-basic .profile-info-left .staff-msg .btn-custom:hover {
        border-color: $blue;
    }

    a {
        color: $blue;

        &:hover {
            color: $blue-hover;
        }
    }

    .nav-tabs.nav-tabs-bottom li a.active {
        border-bottom-color: $blue;
    }

    .custom-modal .modal-content .modal-header .btn-close {
        background-color: $blue;
        border: 1px solid $blue;
    }

    .custom-modal .modal-content .modal-header .btn-close:hover {
        background-color: transparent;
        color: $blue;
    }

    .breadcrumb .breadcrumb-item.active {
        color: $blue;
    }

    ::selection {
        background: $blue;
        color: $white;
        text-shadow: none;
    }
}


