[data-layout-style="detached"] {
    &[data-layout="horizontal"] {
        .main-wrapper {
            max-width: 100%;
            padding: 0;

            .sidebar {
                top: 60px;
                border-radius: 0;
                left: 0;
                position: relative;
            }
        }
    }
}

//Layout Width detached

[data-layout-style="detached"] {
    .main-wrapper {
        max-width: 95%;
        margin: 0 auto;
        padding-left: 1.5rem;
        position: relative;

        .sidebar {
            position: fixed;
            top: 80px;
            border-radius: 10px;
            left: 50px;

            &.sidebar-twocol {
                top: 30px;
                left: 50px;
                position: absolute;
            }

            @include respond-below(custom991) {
                left: 0;
            }
        }
    }

    .sidebar .sidebar-menu,
    .two-col-bar .sidebar-menu {
        padding: 0;
    }

    #toggle_btn {
        display: none;
    }

}

//Layout Dark

[data-layout-mode="dark"] {
    body {
        background-color: $body-dark-bg;
        color: $secondary-300;

        .stats-box {
            background-color: $black-bg;
            border: 1px solid $dark-border;
        }

        .card {
            color: $secondary-300;
            background-color: $body-dark-bg !important;

            .card-body {
                background-color: $body-dark-bg;
            }
        }

        .disabled>.page-link,
        .page-link.disabled {
            --bs-pagination-disabled-color: #6c757d;
        }

        .w-sidebar ul a {
