[data-layout=horizontal] {
    body {
        .sidebar {
            width: 100% !important;
            bottom: inherit;

            .sidebar-menu ul li span {
                display: inline-block !important;
            }

            .slimScrollDiv,
            .sidebar-inner {
                height: auto !important;
                overflow: inherit !important;
            }

            .hidden {
                display: none !important;
            }

            .sidebar-menu {
                padding: 0 !important;
                height: 60px;
                display: flex;
                align-items: center;
            }

            .sidebar-vertical {
                display: none !important;
            }

            .sidebar-horizantal {
                display: flex !important;
            }

            .sidebar-menu ul li a {
                flex-direction: inherit;
                text-align: left;
            }

            .sidebar-menu .menu-title {
                display: none !important;
            }

            ul li span {
                display: inline-block !important;
            }

            .hidden-links {
                .submenu {
                    ul {
                        box-shadow: 0 5px 4px rgba(0, 0, 0, 0.2);
                        border: 1px solid #d5d5d5;
                    }
                }

                li {
                    a {
                        &:hover {
                            color: $secondary-900;
                        }
                    }

                }
            }

            .sidebar-menu {
                ul {
                    display: flex;
                    align-items: center;
                    -webkit-align-items: center;

                    li {
                        &.submenu {
                            .noti-dot {
                                &:before {
                                    display: none;
                                }
                            }
                        }

                        a {
                            .menu-arrow {
                                right: 0;
                            }
                        }

                    }

                    ul {
                        display: block;
                        width: 200px;
                        background-color: $secondary-800;
                        @include position(absolute, 45px, null, null, 0);

                        li {
                            a {
                                .menu-arrow {
                                    right: 15px;
                                }
                            }
                        }

                        a {
                            padding: 8px 15px;
                        }
                    }
                }
            }

            .list-inline-item {
                li {
                    a:hover {
                        color: $white;
                    }
                }
            }
        }

        #sidebar-view,
        #sidebar-size,
        #layout-width,
        #layout-position {
            display: none;
        }

        #toggle_btn {
            display: none;
        }

        ul.hidden-links {
            position: absolute !important;
            right: 0;
            background: $white;
            width: 100%;
            top: 60px;
            justify-content: flex-start;
            align-items: flex-start !important;
            flex-wrap: wrap;
            border-top: 1px dashed $light-900 ;
            padding: 12px 3px;
            box-shadow: 0 5px 4px rgb(0 0 0 / 20%);

            ul {
                left: 0 !important;
                position: absolute !important;
                top: 40px !important;
                background-color: $white !important;
                width: 100% !important;
                z-index: 9999;

                li {
                    a {
                        width: 100%;

                        &:hover {
                            color: $secondary-900;
                        }
                    }
                }
            }

            >li {
                width: 20%;

                a {
                    display: block;
                    padding: 5px 15px;
                }
            }

            li a .menu-arrow {
                right: 10px !important;
            }
        }

        .sidebar .sidebar-menu ul li a {
            padding: 10px 15px;
        }

        .greedy ul.list-inline-item.list-unstyled.links {
            width: 95%;

        }

        .greedy {
            >button {
                display: block;
            }
        }

        nav.greedy button {
            align-self: stretch;
            transition: all .4s ease-out;
            padding: 5px 0 0 5px;
            outline: 0;
            border: 0;
            font-size: 14px;
            font-weight: $font-weight-bold;
            background-color: transparent;
            color: $white;
            min-width: 100px;
            margin-right: 20px;
        }

        nav.greedy {
            position: relative;
            height: 45px;
            display: -webkit-box;
            display: -ms-flexbox;
            display: flex;
            align-items: center;
            -webkit-box-align: center;
            -ms-flex-align: center;
            justify-content: flex-start;
            -webkit-justify-content: start;
            -ms-flex-pack: start;
        }

        nav.greedy ul.hidden-links.hidden {
            display: none;
        }

        .page-wrapper {
            margin: 0;
            padding-top: 120px;
        }

        .sidebar .sidebar-menu ul ul ul {
            position: absolute;
            right: -200px;
            top: 0;
            left: auto;
        }

        .sidebar ul ul li {
            display: inline-block !important;
            width: 100%;
        }

        .hidden-links {
            .menu-title {
                display: inline-block !important;
                color: $secondary-900 !important;
            }
        }
    }
}

